// Energy-industry slider =======================================================================================================================================================

if(document.querySelectorAll('.energy-industry__slider').length > 0) {

  let sliderCover = document.querySelectorAll('.energy-industry__slider');

  sliderCover.forEach(slider => {
    let swiper = new Swiper(slider, {
      modules: [Pagination, EffectCoverflow, Autoplay, Navigation],
      slidesPerView: 1,
      loop: true,
      autoHeight: true,

      autoplay: {
        delay: 5000,
      },

      effect: 'coverflow',
      coverflowEffect: {
        rotate: 30,
        slideShadows: true,
      },

      pagination: {
        el: '.swiper-pagination',
        clickable: true,
      },

      navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev',
      },

      breakpoints: {
        // when window width is >= 320px
        320: {
          spaceBetween: 120,
        },
        // when window width is >= 480px
        480: {
          spaceBetween: 120
        },
        // when window width is >= 640px
        640: {
          spaceBetween: 180
        }
      }

    });
  })
}

// FIXED HEADER ========================================================================================================================================================

const header = document.querySelector(".header");

function scrollHeader() {
  if (document.documentElement.scrollTop > 1 || document.body.scrollTop > 1) {
    header.classList.add("scroll");
  } else {
    header.classList.remove("scroll");
  }
}

window.addEventListener("scroll", () => {
  scrollHeader();
});

// ACTIVITY CLASS ========================================================================================================================================================

if (document.querySelectorAll(".header .nav ul a").length > 0) {
  const menuContainer = document.querySelectorAll(".header .nav ul a");

  menuContainer.forEach(function (el) {
    if (window.location.pathname.indexOf(el.getAttribute("href")) > -1) {
      el.classList.add("active");
    } else {
      el.classList.remove("active");
    }
  });
}

// GET YEAR ========================================================================================================================================================

if (document.querySelectorAll(".footer__copyright").length > 0) {
  let yearSpan = document.querySelector(".year");
  let date = new Date();
  let yearFull = date.getFullYear();

  yearSpan.innerHTML = yearFull;
}

// STATISTIC ANIMATE ========================================================================================================================================================
(function ($) {
  let a = 0;
  let figuresWrap = $(".statistic");
  $(document).ready(function () {
    function count() {
      if (figuresWrap.length > 0) {
        let oTop = figuresWrap.offset().top - window.innerHeight + 150;
        if (a == 0 && $(window).scrollTop() > oTop) {
          $(".statistic__item > .statistic__number b").each(function () {
            let $this = $(this),
              countTo = $this.text();
            $({
              // countNum: $this.text()
              countNum: 0,
            }).animate(
              {
                countNum: countTo,
              },
              {
                duration: 3000,
                easing: "swing",
                step: function () {
                  $this.text(Math.floor(this.countNum));
                },
                complete: function () {
                  $this.text(this.countNum);
                  //alert('finished');
                },
              }
            );
          });
          a = 1;
        }
      }
    }
    count();
    $(window).on("scroll", function () {
      count();
    });
  });
})(jQuery);


// Transformation header-btn ========================================================================================================================================================

if (document.querySelectorAll('.header').length > 0) {
  const headerBtn = document.querySelector('.header__btn'),
        headerWrap = document.querySelector('.header__wrap'),
        headerNav = document.querySelector('.nav');

  function transformationHeaderBtn () {
    if (window.innerWidth < 992) {
      headerNav.append(headerBtn)
    } else {
      headerWrap.append(headerBtn);
    }
  }

  document.addEventListener('DOMContentLoaded', () => {
    transformationHeaderBtn();
  })

  window.addEventListener('resize', () => {
    transformationHeaderBtn();
  })
}


// Globe ========================================================================================================================================================

import * as THREE from '../../node_modules/three/build/three.module.js';

//import THREE from '../../node_modules/three/build/three.cjs';
import { OrbitControls } from '../../node_modules/three/examples/jsm/controls/OrbitControls.js';
import { CSS2DRenderer, CSS2DObject } from '../../node_modules/three/examples/jsm/renderers/CSS2DRenderer';
//import * as THREE from "https://cdn.skypack.dev/three@0.136.0";
//import { OrbitControls } from "https://cdn.skypack.dev/three@0.136.0/examples/jsm/controls/OrbitControls";
//import { CSS2DRenderer, CSS2DObject } from 'https://cdn.skypack.dev/three@0.136.0/examples/jsm/renderers/CSS2DRenderer.js';

if (document.querySelectorAll('.hero__globe-wrap').length > 0) {
  let scene = new THREE.Scene();
  let camera = new THREE.PerspectiveCamera(45, 800 / 750, 1, 800);
  camera.position.set(0.5, 0.5, 1).setLength(14);
  let renderer = new THREE.WebGLRenderer({
    antialias: true
  });
  renderer.setSize(800, 750);
  renderer.setClearColor(0xffffff, 0);
  document.querySelector('.hero__globe').appendChild(renderer.domElement);


  let labelRenderer = new CSS2DRenderer();
  labelRenderer.setSize( 800, 750);
  labelRenderer.domElement.style.position = 'absolute';
  labelRenderer.domElement.style.top = '0px';
  document.querySelector('.hero__globe-wrap').appendChild( labelRenderer.domElement );

  window.addEventListener("resize", onWindowResize);
  window.addEventListener("DOMContentLoaded", onDocumentLoad);

  let controls = new OrbitControls(camera, labelRenderer.domElement);
  controls.enablePan = false;
  controls.minDistance = 6;
  controls.maxDistance = 15;
  controls.enableDamping = true;
  controls.autoRotate = true;
  controls.autoRotateSpeed *= 0.25;
  controls.enableZoom = false;

  let globalUniforms = {
    time: { value: 10 }
  };

  // <GLOBE>
  // https://web.archive.org/web/20120107030109/http://cgafaq.info/wiki/Evenly_distributed_points_on_sphere#Spirals

  let imgData = `data:image/jpeg;base64,`;

  let counter = 200000;
  let rad = 5;
  let sph = new THREE.Spherical();

  let r = 0;
  let dlong = Math.PI * (3 - Math.sqrt(5));
  let dz = 2 / counter;
  let long = 0;
  let z = 1 - dz / 2;

  let pts = [];
  let clr = [];
  let c = new THREE.Color();
  let uvs = [];

  for (let i = 0; i < counter; i++) {
    r = Math.sqrt(1 - z * z);
    let p = new THREE.Vector3(
      Math.cos(long) * r,
      z,
      -Math.sin(long) * r
    ).multiplyScalar(rad);
    pts.push(p);
    z = z - dz;
    long = long + dlong;

    c.setHSL(0.45, 0.5, Math.random() * 0.25 + 0.25);
    //c.setHSL(0.5, 1, 0.25 + 0.25);
    c.toArray(clr, i * 3);

    sph.setFromVector3(p);
    uvs.push((sph.theta + Math.PI) / (Math.PI * 2), 1.0 - sph.phi / Math.PI);
  }

  let g = new THREE.BufferGeometry().setFromPoints(pts);
  g.setAttribute("color", new THREE.Float32BufferAttribute(clr, 3));
  g.setAttribute("uv", new THREE.Float32BufferAttribute(uvs, 2));
  let m = new THREE.PointsMaterial({
    size: 0.1,
    vertexColors: true,
    onBeforeCompile: (shader) => {
      shader.uniforms.globeTexture = {
        value: new THREE.TextureLoader().load(imgData)
      };
      shader.vertexShader = `
        uniform sampler2D globeTexture;
        varying float vVisibility;
        varying vec3 vNormal;
        varying vec3 vMvPosition;
        ${shader.vertexShader}
      `.replace(
        `gl_PointSize = size;`,
        `
          vVisibility = texture(globeTexture, uv).g; // get value from texture
          gl_PointSize = size * (vVisibility < 0.5 ? 1. : 0.75); // size depends on the value
          vNormal = normalMatrix * normalize(position);
          vMvPosition = -mvPosition.xyz;
          gl_PointSize *= 0.4 + (dot(normalize(vMvPosition), vNormal) * 0.6); // size depends position in camera space
        `
      );
      //console.log(shader.vertexShader);
      shader.fragmentShader = `
        varying float vVisibility;
        varying vec3 vNormal;
        varying vec3 vMvPosition;
        ${shader.fragmentShader}
      `.replace(
        `vec4 diffuseColor = vec4( diffuse, opacity );`,
        `
          bool circ = length(gl_PointCoord - 0.5) > 0.5; // make points round
          bool vis = dot(vMvPosition, vNormal) < 0.; // visible only on the front side of the sphere
          if (circ || vis) discard;

          vec3 col = diffuse + (vVisibility > 0.5 ? 0.5 : 0.); // make oceans brighter

          vec4 diffuseColor = vec4( col, opacity );
        `
      );
      //console.log(shader.fragmentShader);
    }
  });
  let globe = new THREE.Points(g, m);
  scene.add(globe);

    // <ICOSAHEDRON>
    let icshdrn = new THREE.Mesh(new THREE.IcosahedronGeometry(rad, 1), new THREE.MeshBasicMaterial({color: 0xffffff, wireframe: false}));
    globe.add(icshdrn);
    // </ICOSAHEDRON>
  // </GLOBE>

  // <Markers>
  const markerCount = 30;
  let markerInfo = []; // information on markers
  let gMarker = new THREE.PlaneGeometry();
  let mMarker = new THREE.MeshBasicMaterial({
    color: 0x00C1C1,
    //color: 0x084046,
    onBeforeCompile: (shader) => {
      shader.uniforms.time = globalUniforms.time;
      shader.vertexShader = `
        attribute float phase;
        varying float vPhase;
        ${shader.vertexShader}
      `.replace(
        `#include <begin_vertex>`,
        `#include <begin_vertex>
          vPhase = phase; // de-synch of ripples
        `
      );
      //console.log(shader.vertexShader);
      shader.fragmentShader = `
        uniform float time;
        varying float vPhase;
        ${shader.fragmentShader}
      `.replace(
        `vec4 diffuseColor = vec4( diffuse, opacity );`,
        `
        vec2 lUv = (vUv - 0.5) * 2.;
        float val = 0.;
        float lenUv = length(lUv);
        val = max(val, 1. - step(0.25, lenUv)); // central circle
        val = max(val, step(0.4, lenUv) - step(0.5, lenUv)); // outer circle

        float tShift = fract(time * 0.5 + vPhase);
        val = max(val, step(0.4 + (tShift * 0.6), lenUv) - step(0.5 + (tShift * 0.5), lenUv)); // ripple

        if (val < 0.5) discard;

        vec4 diffuseColor = vec4( diffuse, opacity );`
      );
      //console.log(shader.fragmentShader)
    }
  });
  mMarker.defines = { USE_UV: " " }; // needed to be set to be able to work with UVs
  let markers = new THREE.InstancedMesh(gMarker, mMarker, markerCount);

  let dummy = new THREE.Object3D();
  let phase = [];
  for (let i = 0; i < markerCount; i++) {
    dummy.position.randomDirection().setLength(rad + 0.1);
    dummy.lookAt(dummy.position.clone().setLength(rad + 1));
    dummy.updateMatrix();
    markers.setMatrixAt(i, dummy.matrix);
    phase.push(Math.random());

    markerInfo.push({
      id: i + 1,
      mag: THREE.MathUtils.randInt(1, 10),
      crd: dummy.position.clone()
    });
  }
  gMarker.setAttribute(
    "phase",
    new THREE.InstancedBufferAttribute(new Float32Array(phase), 1)
  );

  scene.add(markers);
  // </Markers>

  // <Label>
  let labelDiv = document.getElementById("markerLabel");

  let closeBtn = document.getElementById("closeButton");
  closeBtn.addEventListener("click", event => {
    labelDiv.classList.add("hidden");
    console.log(labelDiv);
  })
  let label = new CSS2DObject(labelDiv);
  label.userData = {
    cNormal: new THREE.Vector3(),
    cPosition: new THREE.Vector3(),
    mat4: new THREE.Matrix4(),
    trackVisibility: () => { // the closer to the edge, the less opacity
      let ud = label.userData;
      ud.cNormal.copy(label.position).normalize().applyMatrix3(globe.normalMatrix);
      ud.cPosition.copy(label.position).applyMatrix4(ud.mat4.multiplyMatrices(camera.matrixWorldInverse, globe.matrixWorld));
      let d = ud.cPosition.negate().normalize().dot(ud.cNormal);
      d = smoothstep(0.2, 0.7, d);
      label.element.style.opacity = d;

      // https://github.com/gre/smoothstep/blob/master/index.js

      function smoothstep (min, max, value) {
        var x = Math.max(0, Math.min(1, (value-min)/(max-min)));
        return x*x*(3 - 2*x);
      };
    }
  }
  scene.add(label);
  // </Label>

  // <Interaction>
  let pointer = new THREE.Vector2();
  let raycaster = new THREE.Raycaster();
  let intersections;
  let divID = document.getElementById("idNum");
  let divMag = document.getElementById("magnitude");
  let divCrd = document.getElementById("coordinates");
  window.addEventListener("pointerdown", event => {
    //pointer.x = ( event.clientX / window.innerWidth ) * 2 - 1;
    //pointer.y = - ( event.clientY / window.innerHeight ) * 2 + 1;
    //raycaster.setFromCamera(pointer, camera);
    intersections = raycaster.intersectObject(markers).filter(m => {
      return (m.uv.subScalar(0.5).length() * 2) < 0.25; // check, if we're in the central circle only
    });
    //console.log(intersections);
    if (intersections.length > 0) {
      let iid = intersections[0].instanceId;
      let mi = markerInfo[iid];
      divID.innerHTML = `ID: <b>${mi.id}</b>`;
      divMag.innerHTML = `Mag: <b>${mi.mag}</b>`;
      divCrd.innerHTML = `X: <b>${mi.crd.x.toFixed(2)}</b>; Y: <b>${mi.crd.y.toFixed(2)}</b>; Z: <b>${mi.crd.z.toFixed(2)}</b>`;
      label.position.copy(mi.crd);
      label.element.animate([
        {width: "0px", height: "0px", marginTop: "0px", marginLeft: "0px"},
        {width: "230px", height: "50px", marginTop: "-25px", marginLeft: "120px"}
      ],{
        duration: 250
      });
      label.element.classList.remove("hidden");
    }

  })
  // </Interaction>

  let clock = new THREE.Clock();

  renderer.setAnimationLoop(() => {
    let t = clock.getElapsedTime();
    globalUniforms.time.value = t;
    label.userData.trackVisibility();
    controls.update();
    renderer.render(scene, camera);
    labelRenderer.render(scene, camera);
  });

  function onWindowResize() {
    //camera.aspect = innerWidth / innerHeight;
    camera.updateProjectionMatrix();

    renderer.setSize(800, 750);
    labelRenderer.setSize(800, 750);
    if (window.innerWidth <= 1500) {
      renderer.setSize(600, 520);
      labelRenderer.setSize( 600, 520);
    }
    if (window.innerWidth <= 1200) {
    }
    if (window.innerWidth <= 768) {
      renderer.setSize(500, 450);
      labelRenderer.setSize(500, 450);
    }
    if (window.innerWidth <= 576) {
      renderer.setSize(450, 400);
      labelRenderer.setSize(450, 400);
    }
    if (window.innerWidth <= 480) {
      renderer.setSize(320, 300);
      labelRenderer.setSize(320, 300);
    }
  }

  function onDocumentLoad() {
    //camera.updateProjectionMatrix();

    renderer.setSize(800, 750);
    labelRenderer.setSize(800, 750);
    if (window.innerWidth <= 1500) {
      renderer.setSize(600, 520);
      labelRenderer.setSize( 600, 520);
    }
    if (window.innerWidth <= 1200) {
    }
    if (window.innerWidth <= 768) {
      renderer.setSize(500, 450);
      labelRenderer.setSize(500, 450);
    }
    if (window.innerWidth <= 576) {
      renderer.setSize(450, 400);
      labelRenderer.setSize(450, 400);
    }
    if (window.innerWidth <= 480) {
      renderer.setSize(320, 300);
      labelRenderer.setSize(320, 300);
    }
  }
}

